import type { EventName } from "@lit-labs/react";
import * as WebComponentModule from "@vaadin/dialog/vaadin-dialog.js";
import * as React from "react";
import { createComponent, WebComponentProps } from "../utils/createComponent.js";
export type DialogEventMap = Readonly<{
    onOpenedChanged: EventName<WebComponentModule.DialogEventMap["opened-changed"]>;
}>;
const events = { onOpenedChanged: "opened-changed" } as DialogEventMap;
export type DialogProps = WebComponentProps<WebComponentModule.Dialog, DialogEventMap>;
export const Dialog = createComponent({
    elementClass: WebComponentModule.Dialog,
    events,
    react: React,
    tagName: "vaadin-dialog"
});
export { WebComponentModule };
